.counter {
  text-align: center;
}

.counter h1 {
  font-size: 2.5em;
  color: #333; /* Heading color */
}

.counter p {
  font-size: 1.2em;
  color: #666; /* Text color */
}

.counter h2 {
  font-size: 2em;
  color: #333; /* Counter value color */
}

.counter button {
  font-size: 1.2em;
  padding: 10px 20px;
  margin: 0 10px;
  background-color: #007bff; /* Button background color */
  color: #fff; /* Button text color */
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.counter button:hover {
  background-color: #0056b3; /* Button background color on hover */
}

